// Small  sm  ≥576px
@mixin media-min-width-SM() {
  @media (min-width: 576px) {
    @content;
  }
}

// Medium  md  ≥768px
@mixin media-min-width-MD() {
  @media (min-width: 768px) {
    @content;
  }
}

// Large  lg  ≥992px
@mixin media-min-width-LG() {
  @media (min-width: 1120px) {
    @content;
  }
}

// Extra large  xl  ≥1200px
@mixin media-min-width-XL() {
  @media (min-width: 1366px) {
    @content;
  }
}

// // Extra extra large  xxl  ≥1400px
// @mixin media-min-width-XXL() {
//   @media (min-width: 1440px) {
//     @content;
//   }
// }

@mixin media-hover() {
  @media(hover: hover) {
    &:hover {
      @content;
    }
  }

  @media(hover: none) {
    &:active {
      @content;
    }
  }
}
